/* ESTILOS EN HOME */

#hero {
    height: 100vh;
    background-image: url('/public/assets/kia-homepage.png');
    background-position: center center;
  }
  
  #hero-content {
    color: white;
    height: 100vh;
    background-color: #0000005e;
  }
  
  #hero-content p {
    font-size: 1.25rem;
    font-weight: 500;
    text-shadow: 2px 2px 4px #333;
  }
  
  #desc {
    background-color: #212121;
    color: white;
    padding: 35px 0px 35px 30px;
  }
  
  #desc p {
    font-size: 1.25rem;
  }
  
  #desc .cta {
    margin-left: 5vw;
    font-weight: 600;
    padding: 7px 20px 7px 20px;
    border-radius: 25px;
    border: 3px solid #bb162b;
    cursor: pointer;
  }
  
  #modelos-destacados {
    background-color: #efefef;
    padding-top: 20px;
  }
  
  #modelos-destacados h2 {
    color: black;
    font-size: 32px;
  }
  
  #list-destacados li img{
    display: block;
    width: 100%;
    height: auto;
    max-width: 680px;
  }
  
  #list-destacados li {
    padding: 10px;
  }
  
  #list-destacados li:nth-child(n+5) {
    width: 20vw !important;
  }
  
  #list-destacados li:nth-child(5) {
    margin-left: 9vw;
  }
  
  #list-destacados li:last-child {
    margin-right: 9vw;
  }
  
  #list-destacados li span {
    font-size: 23px;
    font-weight: 200;
  }
  
  #popup {
    padding-top: 60px;
    padding-bottom: 80px;
  }
  
  #popup h2 {
    color: black;
    font-size: 32px;
    margin-bottom: 10px;
  }
  
  #popups-content {
    max-width: 1140px;
  }
  
  #popups-content a {
    padding: 10px;
    text-decoration: none;
    color: black;
  }
  
  #popups-content h3 {
    font-size: 23px;
  }
  
  #popups-content img{
    width: 100%;
    height: auto;
  }
  
  #contact {
    background-color: black;
    color: white;
    padding-top: 20px;
  }
  
  #ubicacion {
    min-height: 470px;
    padding: 0px 30px 0px 30px;
  }
  
  #ubicacion iframe {
    height: 350px;
  }
  
  footer {
    background-color: black;
    border-top: 2px solid white;
    padding: 10px 0px 10px 0px;
  }
  
  footer span {
    font-size: 23px;
    color: white;
  }